import React, { useState } from 'react';
import { Icon } from '@iconify/react';
import arrowBackFill from '@iconify/icons-eva/arrow-back-fill';
// material
import { styled } from '@mui/material/styles';
import {
  Box,
  Button,
  Card,
  Grid,
  Typography,
  OutlinedInput
} from '@mui/material';

import account from '../../../_mocks_/account';

// ----------------------------------------------------------------------

const RootStyle = styled(Card)({
  maxWidth: '500px',
  padding: '3rem 2rem',
  display: 'flex',
  flexDirection: 'column',
  rowGap: '1.5rem',
  overflow: 'hidden'
});

// ----------------------------------------------------------------------

export default function ProfileForm() {
  const [name, setName] = useState(account.displayName);
  const [email, setEmail] = useState(account.email);
  const [showPassForm, setShowPassForm] = useState(false);

  const handleName = (e) => {
    setName(e.target.value);
  };

  const handleEmail = (e) => {
    setEmail(e.target.value);
  };

  return (
    <RootStyle>
      <Box sx={{ display: 'flex', flexDirection: 'row', alignItems: 'center', columnGap: '5px' }}>
        <Box component={Icon} icon={arrowBackFill} sx={{ width: 20, height: 20, mr: 0.5, cursor: 'pointer' }} />
        <Typography variant="body1" fontWeight="bold">
          Profile Settings
        </Typography>
      </Box>
      <Box>
        <Grid container spacing={2}>
          <Grid item xs={6} md={6} display="flex" alignItems="center">
            <Typography>Name:</Typography>
          </Grid>
          <Grid item xs={6} md={6} display="flex" alignItems="center">
            <OutlinedInput
              value={name}
              onChange={(e) => handleName(e)}
              sx={{
                '& input': {
                  padding: '7px 14px'
                }
              }}
            />
          </Grid>
          <Grid item xs={6} md={6} display="flex" alignItems="center">
            <Typography>Email:</Typography>
          </Grid>
          <Grid item xs={6} md={6} display="flex" alignItems="center">
            <OutlinedInput
              value={email}
              onChange={(e) => handleEmail(e)}
              sx={{
                '& input': {
                  padding: '7px 14px'
                }
              }}
            />
          </Grid>
        </Grid>
      </Box>
      <Box
        sx={{
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'flex-start',
          columnGap: '10px'
        }}
      >
        <Button variant="outlined" size="small">
          Save
        </Button>
      </Box>
      <Box
        sx={{
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'flex-start',
          columnGap: '10px'
        }}
      >
        <Button size="small" onClick={() => setShowPassForm(true)}>
          Update Password
        </Button>
      </Box>

      {showPassForm ? (
        <>
          <Box>
            <Grid container spacing={2}>
              <Grid item xs={6} md={6} display="flex" alignItems="center">
                <Typography>Current Password:</Typography>
              </Grid>
              <Grid item xs={6} md={6} display="flex" alignItems="center">
                <OutlinedInput
                  type="password"
                  sx={{
                    '& input': {
                      padding: '7px 14px'
                    }
                  }}
                />
              </Grid>
              <Grid item xs={6} md={6} display="flex" alignItems="center">
                <Typography>New Password:</Typography>
              </Grid>
              <Grid item xs={6} md={6} display="flex" alignItems="center">
                <OutlinedInput
                  type="password"
                  sx={{
                    '& input': {
                      padding: '7px 14px'
                    }
                  }}
                />
              </Grid>
              <Grid item xs={6} md={6} display="flex" alignItems="center">
                <Typography>New Password again:</Typography>
              </Grid>
              <Grid item xs={6} md={6} display="flex" alignItems="center">
                <OutlinedInput
                  type="password"
                  sx={{
                    '& input': {
                      padding: '7px 14px'
                    }
                  }}
                />
              </Grid>
            </Grid>
          </Box>
          <Box
            sx={{
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'flex-start',
              columnGap: '10px'
            }}
          >
            <Button variant="contained" size="small">
              Update
            </Button>
            <Button variant="outlined" size="small">
              Cancel
            </Button>
          </Box>
        </>
      ) : (
        <></>
      )}
    </RootStyle>
  );
}
