// material
import { styled } from '@mui/material/styles';
import { Box, Button, Card, Grid, Typography, Slider } from '@mui/material';

import { Link as RouterLink } from 'react-router-dom';

// ----------------------------------------------------------------------
const marks = [
  {
    value: 45
  },
  {
    value: 50
  },
  {
    value: 55
  },
  {
    value: 60
  },
  {
    value: 65
  },
  {
    value: 70
  },
  {
    value: 75
  },
  {
    value: 80
  },
  {
    value: 85
  },
  {
    value: 90
  }
];

const RootStyle = styled(Card)({
  maxWidth: '500px',
  padding: '3rem 2rem',
  display: 'flex',
  flexDirection: 'column',
  rowGap: '1.5rem',
  overflow: 'hidden'
});

// ----------------------------------------------------------------------
const valuetext = (value) => {
  return `${value}°C`;
};
export default function ZoneDetail() {
  return (
    <RootStyle>
      <Box sx={{ display: 'flex', flexDirection: 'row', columnGap: '3rem', alignItems: 'center' }}>
        <Typography variant="span" sx={{ fontSize: '16px' }}>
          IONIQ Thermostat
        </Typography>
        <Typography sx={{ fontWeight: '700', flexGrow: 1 }}>Bedroom</Typography>
      </Box>
      <Box>
        <Grid container spacing={2}>
          <Grid item xs={6} md={6}>
            <Typography>Setpoint</Typography>
          </Grid>
          <Grid item xs={6} md={6}>
            <Typography>76°</Typography>
          </Grid>
        </Grid>
      </Box>
      <Box>
        <Slider
          aria-labelledby="track-inverted-slider"
          getAriaValueText={valuetext}
          defaultValue={76}
          marks={marks}
          min={45}
          max={90}
        />
      </Box>
      <Box>
        <Grid container spacing={2}>
          <Grid item xs={6} md={6} display="flex" alignItems="center">
            <Typography>Linked controller:</Typography>
          </Grid>
          <Grid item xs={6} md={6} display="flex" alignItems="center">
            <Button
              variant="outlined"
              fullWidth
              sx={{
                paddingX: '2rem',
                display: 'flex',
                justifyContent: 'space-evenly',
                columnGap: '15px',
                borderColor: '#1f2937',
                color: '#1f2937',
                '&:hover': {
                  backgroundColor: '#1f2937',
                  borderColor: '#1f2937',
                  color: '#fff'
                }
              }}
            >
              IONIQ_CONTROLLER_TEST
            </Button>
          </Grid>
          <Grid item xs={6} md={6} display="flex" alignItems="center">
            <Typography>Sensor temp:</Typography>
          </Grid>
          <Grid item xs={6} md={6} display="flex" alignItems="center">
            <Typography>86°</Typography>
          </Grid>
          <Grid item xs={6} md={6} display="flex" alignItems="center">
            <Typography>Status:</Typography>
          </Grid>
          <Grid item xs={6} md={6} display="flex" alignItems="center">
            <Typography>online</Typography>
          </Grid>
          <Grid item xs={6} md={6} display="flex" alignItems="center">
            <Typography>Schedule:</Typography>
          </Grid>
          <Grid item xs={6} md={6} display="flex" alignItems="center">
            <Typography>
              <Button
                variant="outlined"
                sx={{
                  paddingX: '1.5rem',
                  display: 'flex',
                  justifyContent: 'space-evenly',
                  columnGap: '15px',
                  borderColor: '#1f2937',
                  color: '#1f2937',
                  '&:hover': {
                    backgroundColor: '#1f2937',
                    borderColor: '#1f2937',
                    color: '#fff'
                  }
                }}
              >
                SETTINGS
              </Button>
            </Typography>
          </Grid>
        </Grid>
      </Box>
      <Box
        sx={{
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'flex-start',
          columnGap: '10px'
        }}
      >
        <Button variant="contained" size="small">
          Save
        </Button>
        <Button variant="outlined" size="small" component={RouterLink} to="/dashboard/app">
          Back
        </Button>
      </Box>
    </RootStyle>
  );
}
