import PropTypes from 'prop-types';
import { Link as RouterLink } from 'react-router-dom';
// material
import {
  Box,
  Button,
  Typography,
} from '@mui/material';

// ----------------------------------------------------------------------

// ----------------------------------------------------------------------

ZoneList.propTypes = {
  zone: PropTypes.object.isRequired,
  index: PropTypes.number
};

export default function ZoneList({ zone, index }) {
  return (
    <Box sx={{ width: '100%', display: 'flex', flexDirection: 'column', rowGap: '5px' }}>
      <Typography variant="body1" fontWeight="bold">
        {zone.name}
      </Typography>
      <Box sx={{ display: 'flex', flexDirection: 'column', rowGap: '10px' }}>
        {zone.item.map((item) => {
          return (
            <Button
              key={item.id}
              variant="outlined"
              fullWidth
              component={RouterLink}
              to="/dashboard/detail"
              sx={{
                paddingX: '2rem',
                display: 'flex',
                justifyContent: 'space-evenly',
                columnGap: '15px',
                borderColor: '#1f2937',
                color: '#1f2937',
                '&:hover': {
                  backgroundColor: '#1f2937',
                  borderColor: '#1f2937',
                  color: '#fff'
                }
              }}
            >
              <Typography variant="span">{item.name}</Typography>
              <Typography variant="span">{item.point}°F</Typography>
            </Button>
          );
        })}
      </Box>
    </Box>
  );
}
